.project-item {
  list-style: none;
  width: 20%;
  position: relative;
  overflow: hidden;
  padding: 5px;
}
.project-item-box {
  height: 100%;
  width: 100%;
  overflow: hidden;
  position: relative;
}

.project-item .projectDetail {
  position: absolute;
  height: 0%;
  width: 0%;
  top: 120%;
  left: 50%;
  background: rgba(14, 13, 14, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  transition: ease 1s all;
}

.project-item:hover .projectDetail,
.projectDetail:hover {
  top: 10px;
  left: 10px;
  height: calc(100% - 20px);
  width: calc(100% - 20px);
  border: 1px dashed;
}

@media screen and (max-width: 1200px) {
  .project-item {
    width: 25%;
  }
}
@media screen and (max-width: 992px) {
  .project-item {
    width: 33.33333%;
  }
}
@media screen and (max-width: 768px) {
  .project-item {
    width: 50%;
  }
}
