/*portfolio */
.portfolio {
  background-image: radial-gradient(rgba(0, 123, 255, 0.25), #ff01d4);
}
.portfolio-box {
  text-align: center;
  padding: 85px 0 60px;
  position: relative;
}

.portfolio-box .list-group-item.active {
  z-index: 0;
}

.portfolio-items {
  margin-left: -5px;
  margin-right: -5px;
}

.category .list-group-item {
  background: transparent;
  color: #fff;
  border: 2px solid #fff;
  border-radius: 25px;
  margin-bottom: 10px;
  margin-left: 5px;
  cursor: pointer;
  font-family: arial;
  font-size: 14px;
  font-weight: bold;
}
.category .active {
  background: #fff;
  color: rgb(48, 44, 44);
}

@media screen and (max-width: 768px) {
  .category {
    overflow-x: auto;
  }
}
